/* 
 * PANELIZR ALERT
 * very very very simple code to display an alert that takes over the content of a panelizr panel
 * ASSUMES YOU ARE IN A PANELIZR PANEL, DOES NOT WORK OUTSIDE OF PANELIZR
 * Only set up so far to handle in-page static content.
 */


; define('core/ef_plugins/panelizr.alert',["jquery", "underscore", "core/ef_plugins/spinnerhelper"], function (jQuery) {

    (function ($, window, document, undefined) {

        var pluginName = 'panelizralert',
            defaults = {
                hideSelector: '', // REQUIRED - selector of child element of panelizr body to hide when this is shown
                showLoadingSpinner: true // show a loading spinner when loading
            };

        // The actual plugin constructor
        function PanelizrAlert(element, options) {
            // grab the element the plugin was called on
            this.element = element;

            // also save the element as a jquery object
            this.$element = $(element);

            // get per-element inline configuration settings
            this.metadata = this.$element.data('panelizralert-options');

            // merge all the configs
            this.options = $.extend({}, defaults, options, this.metadata);

            // save the defaults
            this._defaults = defaults;

            // save the plugin name
            this._name = pluginName;

            // call init function
            this.init();
        }

        // Define this sucker
        PanelizrAlert.prototype = {
            init: function () {
                var plugin = this;

                // set open flag
                plugin.isOpen = false;

                // get parent panel

                if ($(this.element.parentElement).hasClass('cta-body')) {
                    plugin.$parentPanelBody = plugin.$element.closest('.cta-body');
                } else {
                    plugin.$parentPanelBody = plugin.$element.closest('.panel-body');
                }

                // get the element to hide
                plugin.$hideMe = plugin.$parentPanelBody.find(plugin.options.hideSelector);

                // make sure that the alert is outside of the hidden element
                plugin.$element.detach();
                plugin.$element.insertAfter(plugin.$hideMe).hide();
            },

            open: function () {
                // re-reference the plugin to avoid this conflicts
                var plugin = this;

                // exit if this is already open
                if (plugin.isOpen) return;

                if (plugin.options.showLoadingSpinner) {
                    plugin.spinner = plugin.$parentPanelBody.spinnerhelper({ 'type': 'overlay', 'delay': 0 }).data('plugin_spinnerhelper');

                    _.delay(function () {
                        plugin.spinner.stop();
                        plugin.$hideMe.hide();
                        plugin.$element.show();
                    }, 500);
                } else {
                    plugin.$hideMe.hide();
                    plugin.$element.show();
                }

                plugin.isOpen = true;

            },

            close: function () {
                // re-reference the plugin to avoid this conflicts
                var plugin = this;

                // exit if this isn't already open
                if (!plugin.isOpen) return;

                plugin.$element.hide();
                plugin.$hideMe.show();

                plugin.isOpen = false;
            }
        };

        // Turn this into a jquery plugin & prevent multiple instantiations
        $.fn[pluginName] = function (options) {
            return this.each(function () {
                if (!$.data(this, 'plugin_' + pluginName)) {
                    $.data(this, 'plugin_' + pluginName,
                        new PanelizrAlert(this, options));
                }
            });
        }

    })(jQuery, window, document);
});
